@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;font-family:"Nunito", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#6a5be2;display:flex;align-items:center;justify-content:center;height:100vh}.app{width:500px;border-radius:30px;padding:40px;background-color:#fff;position:relative}@media (max-width: 700px){.app{width:380px;padding:20px}}.app__container{display:flex;flex-direction:column;grid-gap:30px;gap:30px;justify-content:center;align-items:center}.app__title{font-size:30px}.app .result{text-align:center}.app .result button{font-weight:bold;font-family:"Nunito", sans-serif;border-radius:30px;border:0;padding:15px 30px;font-size:20px;background-color:#f00067;color:#fff;margin-top:20px;cursor:pointer}.app .result h2{margin-bottom:0}.app .result img{width:150px}.app .progress{height:10px;border-radius:30px;background-color:#e8e8e8;margin-bottom:25px}.app .progress__inner{height:100%;border-radius:30px;width:80%;background:#12e730;transition:all 0.3s ease-in-out;background:linear-gradient(90deg, #12e730 0%, #00d4ff 100%)}.app:before,.app:after{content:"";display:block;height:50px;border-bottom-left-radius:30px;border-bottom-right-radius:30px;position:absolute;left:50%;transform:translateX(-50%)}.app:before{width:95%;bottom:-10px;background-color:rgba(255,255,255,0.8);z-index:0}.app:after{width:90%;bottom:-20px;background-color:rgba(255,255,255,0.3);z-index:1}.app h1{margin:0}.app ul{list-style:none;padding:0}.app ul li{padding:15px;border:2px solid rgba(0,0,0,0.1);border-radius:15px;margin-bottom:10px;cursor:pointer;transition:all 0.1s ease-in-out}.app ul li:hover{border:2px solid rgba(0,0,0,0.3)}.name-block__form{display:flex;grid-gap:10px;gap:10px}.history{margin-top:40px}.title{font-size:48px}

